@font-face {
    font-family: 'MuseoSansCyrl';
    src: url('../fonts/MuseoSansCyrl.otf') format('woff');
    font-weight: 100;
    font-style: normal;
}

@font-face {
    font-family: 'MuseoSansCyrl';
    src: url('../fonts/MuseoSansCyrl_0.otf') format('woff');
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: 'MuseoSansCyrl';
    src: url('../fonts/MuseoSansCyrl_1.otf') format('woff');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'MuseoSansCyrl';
    src: url('../fonts/MuseoSansCyrl_2.otf') format('woff');
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'MuseoSansCyrl';
    src: url('../fonts/MuseoSansCyrl_3.otf') format('woff');
    font-weight: 900;
    font-style: normal;
}

$MSC: 'MuseoSansCyrl';

$ColorY: #FFD241;
$ColorR: #CB0037;

@mixin BackGY($weight:900) {
    background: $ColorY;
    font-weight: $weight
}

@mixin BackGR {
    background: $ColorR;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: $MSC;
    font-weight: 700;
}

.container {
    max-width: 780px;
    margin: 0 auto;
}

.header {
    @include BackGY;
    position: fixed;
    width: 100%;
    height: 70px;
    z-index: 10;
}

.menu {
    --color: #fff;
    width: 30px;
    height: 30px;
    padding: 0;
    margin: 0;
    outline: none;
    position: absolute;
    border: none;
    background: none;
    right: 25px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    -webkit-appearence: none;
    -webkit-tap-highlight-color: transparent;
        svg {
            width: 64px;
            height: 48px;
            top: -6px;
            left: -14px;
            stroke: var(--color);
            stroke-width: 4px;
            stroke-linecap: round;
            stroke-linejoin: round;
            fill: none;
            display: block;
            position: absolute;
            path {
                transition: stroke-dasharray var(--duration, .85s) var(--easing, ease) var(--delay, 0s), stroke-dashoffset var(--duration, .85s) var(--easing, ease) var(--delay, 0s);
                stroke-dasharray: var(--array-1, 26px) var(--array-2, 100px);
                stroke-dashoffset: var(--offset, 126px);
                transform: translateZ(0);
                &:nth-child(2) {
                    --duration: .7s;
                    --easing: ease-in;
                    --offset: 100px;
                    --array-2: 74px;
                }
                &:nth-child(3) {
                    --offset: 133px;
                    --array-2: 107px;
                }
            }
        }

        &.active {
            svg {
                path {
                    --offset: 57px;
                    &:nth-child(1),
                    &:nth-child(3) {
                        --delay: .15s;
                        --easing: cubic-bezier(.2, .4, .2, 1.1);
                    }
                    &:nth-child(2) {
                        --duration: .4s;
                        --offset: 2px;
                        --array-1: 1px;
                    }
                    &:nth-child(3) {
                        --offset: 58px;
                    }
                }
            }
        }
}

.travel {
    padding-top: 115px;
}

.title {
    text-align: center;
    font-size: 36px;
    line-height: 43px;
    font-weight: 900;
}

.travel__countries {
    padding-top: 55px;
    display: flex;
    flex-wrap: wrap;
}

.travel__tour {
    max-width: 380px;
    width: 100%;
    margin-bottom: 45px;
}

.travel__tour:nth-child(even) {
    margin-left: 20px;
}

.travel__description {
    position: relative;
    margin-bottom: 10px;
}

.travel__description-wrapper {
    height: 100%;
    width: 100%;
}

.travel__description-photo {
    display: flex;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.travel__description-price {
    position: absolute;
    @include BackGY;
    bottom: 0;
    left: 0;
    padding: 14px 26px;
    font-size: 18px;
}

.country {
    text-transform: uppercase;
    font-size: 9px;
    color: #C5B223;
    margin-bottom: 10px;
}

.tagline {
    color: black;
    font-size: 18px;
    margin-bottom: 20px;
}

.route {
    font-size: 10px;
    line-height: 13px;
    color: #8C8C8C;
    margin-bottom: 15px;
}

.info {
    font-size: 10px;
    color: #000000;
    margin-bottom: 15px;
}

.info__timing {
    display: flex;
}
.time {
    margin-right: 8px;
}
.avia {
    width: 130px;
    border-radius: 3px;
    padding: 3px 0;
    text-align: center;
    font-size: 10px;
    color: #FFFFFF;
    @include BackGR;
}

.request__title {
    margin-top: 10px;
    margin-bottom: 70px;
}

.formText {
    font-size: 10px;
    color: #000000;
    font-weight: 700;
}

.formTitles {
    font-size: 10px;
    color: #000000;
    font-weight: 500;
}

.uppercase {
    text-transform: uppercase;
}